body {
  background-color:darkcyan;
}

.App {
  background-color: cadetblue;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input {
  background-color:darkgray;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}

#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus,#to-do-form  button:focus {
  outline: none;
}

#to-do-form  button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: darkgreen;
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}